.top-slider {
  display: block;
  position: relative;
  width: 100%;
  float: left;
  .slider {
    display: block;
    position: relative;
    width: 100%;
    overflow: hidden;
    float: left;
    &__item {
      display: block;
      position: relative;
      float: left;
      width: 100%;
      height: 696px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      &--descr {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        width: 100%;
        height: 310px;
        top: 286px;
        left: 0;
        z-index: 2;
      }
      &--title {
        display: block;
        position: relative;
        float: left;
        width: 530px;
        margin: 0;
        text-align: center;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.26);
        color: #000000;
        font-family: $HelveticaNeueCyrThin;
        font-size: 36px;
        line-height: 47px;
      }
      &--subtitle {
        display: block;
        position: relative;
        float: left;
        width: 530px;
        margin: 0;
        text-align: center;
        color: #000000;
        font-family: $HelveticaNeueCyrLight;
        font-size: 18px;
        line-height: 30px;
      }
    }
    .owl-nav{
      display: block;
      position: absolute;
      width: 100%;
      height: 94px;
      left: 0;
      top: calc(50% - (94px/2));
      .owl-prev{
        display: block;
        position: relative;
        float: left;
        width: 46px;
        height: 100%;
        overflow: hidden;
        color: transparent;
        background-image: url("../img/icons/top-arrs.png");
        background-repeat: no-repeat;
        background-position: 25px 0;
      }
      .owl-next{
        display: block;
        position: relative;
        float: right;
        width: 46px;
        height: 100%;
        color: transparent;
        background-image: url("../img/icons/top-arrs.png");
        background-repeat: no-repeat;
        background-position: -43px 0;
      }
    }
    .owl-dots {
      display: flex;
      position: relative;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      float: left;
      margin-top: -95px;
    }
    .owl-dot {
      display: block;
      position: relative;
      float: left;
      width: 16px;
      height: 16px;
      background-color: transparent;
      border: 2px solid #1f4b65;
      border-radius: 50%;
      margin: 0 7px;
    }
    .owl-dot.active {
      display: block;
      position: relative;
      float: left;
      width: 16px;
      height: 16px;
      background-color: transparent;
      border: 2px solid #1f4b65;
      border-radius: 50%;
      margin: 0 7px;
      &:before {
        display: block;
        position: absolute;
        content: "";
        top: 2px;
        left: 2px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #1f4b65;
      }
    }
  }
}