.why{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  background-color: #fff;
  .work__title{
    span{
      font-size: 48px;
    }
  }
  &__line{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    padding: 0 16%;
    @include border-box;
    width: 100%;
    position: relative;
    float: left;
  }
  &__item{
    display: flex;
    flex: 1 0 25%;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    position: relative;
    float: left;
    padding: 0 31px;
    @include border-box;
  }
  &__icon{
    display: block;
    position: relative;
    float: left;
    margin-bottom: 14px;
    width: 87px;
    height: 87px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__descr{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    margin-bottom: 22px;
    color: #585858;
    text-align: center;
    font-family: $HelveticaNeueCyrLight;
    font-size: 20px;
    line-height: 29px;
  }
}