.start-now{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  background-color: #f8f8f8;
  padding: 25px 0;
  .work__title{
    margin-bottom: 0;
    margin-top: 15px;
  }
  form{
    width: 45%;
    float: none;
    margin: 15px auto;
    .form-row{
      width: 100%;
      float: left;

    }
    input{
      float: left;
      width: 244px;
      height: 47px;
      display: inline-block;
      background-color: #fff;
      padding-left: 15px;
      border: none;
      margin: 0 20px;
      &:placeholder{
        color: #605f5f;
        font-size: 18px;
        font-family: $HelveticaNeueCyrLight;

      }
    }
    button{
      min-width: 121px;
      padding: 15px 25px;
      box-sizing: border-box;
      border-radius: 2px;
      background-image: -webkit-linear-gradient(#01c462 0%, #1fc491 100%);
      background-image: -o-linear-gradient(#01c462 0%, #1fc491 100%);
      background-image: linear-gradient(#01c462 0%, #1fc491 100%);
      font-size: 20px;
      font-family: $HelveticaNeueCyrLight;
      color: #fff;
      display: inline-block;
      margin: 50px auto 0;
      float: none;
      border: none;
      cursor: pointer;
      outline: none;
    }
  }
}

.medium-title{
  color: #585858;
  font-size: 24px;
  font-family: $HelveticaNeueCyrLight;
  width: 100%;
  text-align: center;
  display: inline-block;

}
