.mob-app{
  display: block;
  position: relative;
  width: 100%;
  float: left;
  overflow: hidden;
  background-image: radial-gradient(circle 678px at 49.97% 515.19%, #fcfcfc 0%, #f8f8f8 100%);
  &:after{
    display: block;
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    width: 0;
    height: 0;
    border-bottom: 449px solid #fff;
    border-right: 1971px solid transparent;
  }
  &__title{
    display: block;
    position: relative;
    float: left;
    width: 100%;
    margin-top: 45px;
    margin-bottom: 0;
    text-align: center;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.26);
    color: #585858;
    font-family: $HelveticaNeueCyrLight;
    font-size: 48px;
    font-weight: 300;
    span{
      font-size: 48px;
    }
  }
  &__subtitle{
    display: block;
    position: relative;
    float: left;
    width: 100%;
    text-align: center;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.26);
    color: #585858;
    font-family: $HelveticaNeueCyrLight;
    font-size: 20px;
    font-weight: 300;
  }
  &__slider{
    display: none;
    position: relative;
    width: 100%;
    float: left;
    &--item{
      display: block;
      position: relative;
      width: 100%;
      height: 425px;
      overflow: hidden;
      float: left;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    .owl-dots {
      display: flex;
      position: relative;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      float: left;
      margin-top: 14px;
    }
    .owl-dot {
      display: block;
      position: relative;
      float: left;
      width: 9px;
      height: 9px;
      background-color: transparent;
      border: 1px solid #1f4b65;
      border-radius: 50%;
      margin: 0 3px;
    }
    .owl-dot.active {
      display: block;
      position: relative;
      float: left;
      width: 9px;
      height: 9px;
      background-color: transparent;
      border: 1px solid #1f4b65;
      border-radius: 50%;
      margin: 0 3px;
      &:before {
        display: block;
        position: absolute;
        content: "";
        top: 1px;
        left: 1px;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background-color: #1f4b65;
      }
    }
  }
  &__left{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 649px;
    float: left;
    z-index: 2;
  }
  &__img{
    display: block;
    position: relative;
    float: left;
    width: 279px;
    height: 548px;
    overflow: hidden;
    margin-top: 42px;
    margin-bottom: 39px;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__right{
    display: block;
    position: relative;
    float: left;
    width: calc(100% - 649px);
    z-index: 2;
  }
  &__tabs-content{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    margin-top: 27px;
    &--item{
      display: block;
      position: relative;
      width: 100%;
      float: left;
      .title{
        display: block;
        position: relative;
        width: 100%;
        float: left;
        text-align: left;
        margin-top: 95px;
        margin-bottom: 14px;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.26);
        color: #585858;
        font-family: $HelveticaNeueCondensedBold;
        font-size: 30px;
      }
      .descr{
        display: block;
        position: relative;
        width: 100%;
        float: left;
        text-align: left;
        margin-top: 0;
        margin-bottom: 62px;
        color: #585858;
        font-family: $HelveticaNeueCyrLight;
        font-size: 20px;
        line-height: 38px;
      }
    }
  }
  &__tabs{
    display: block;
    position: relative;
    width: 100%;
    float: left;
    &--nav{
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: flex-start;
      width: 100%;
      position: relative;
      float: left;
      list-style: none;
      padding: 0;
      margin: 0;
      .tab{
        position: relative;
        display: block;
        float: left;
        margin-right: 16px;
        a{
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          align-items: center;
          width: 100%;
          position: relative;
          float: left;
          border-bottom: 3px solid transparent;
          @include transition(0.4s);
          /*&:hover{
            .icon{
              width: 76px;
              height: 76px;
              margin-top: -13px;
              @include transition(0.4s);
            }
            .text{
              display: block;
            }
          }*/
          .icon{
            display: block;
            position: relative;
            float: left;
            width: 48px;
            height: 48px;
            overflow: hidden;
            margin-top: 0;
            @include transition(0.4s);
            img{
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }
          .text{
            display: none;
            position: relative;
            float: left;
            width: 100%;
            margin-top: 12px;
            text-align: center;
            text-shadow: 0 1px 1px rgba(0, 0, 0, 0.26);
            color: #585858;
            font-family: $HelveticaNeueCyrLight;
            font-size: 14px;
          }
        }
        a.active{
          .icon{
            width: 76px;
            height: 76px;
            margin-top: -13px;
          }
          .text{
            display: block;
          }
        }
      }
    }
  }
  &__hover{
    display: none;
    position: relative;
    width: 100%;
    float: left;
    &--trigger{
      display: block;
      position: relative;
      width: 100%;
      float: left;
      margin-bottom: 8px;
      .icon{
        display: block;
        position: relative;
        float: left;
        width: 47px;
        height: 48px;
        margin-left: 24px;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      .title{
        display: block;
        position: relative;
        float: left;
        width: calc(100% - (47px + 24px + 14px + 24px));
        color: #585858;
        font-family: $HelveticaNeueCondensedBold;
        font-size: 20px;
        margin-top: 14px;
        padding-left: 19px;
        @include border-box;
      }
      .shevron{
        display: block;
        position: relative;
        width: 14px;
        height: 7px;
        margin-left: 24px;
        margin-top: 16px;
        float: left;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
    &--box{
      display: none;
      position: relative;
      width: 100%;
      float: left;
      p{
        display: block;
        position: relative;
        width: 100%;
        float: left;
        margin: 0;
        padding: 8px 33px;
        @include border-box;
        color: #585858;
        font-family: $HelveticaNeueCyrLight;
        font-size: 12px;
        font-weight: 300;
        line-height: 20px;
      }
    }
  }
}
