.header{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  min-height: 526px;
  max-height: 100%;
  background-image: url('../img/header-bg.png');
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  padding-bottom: 35px;
  box-sizing: border-box;
  &__logo{
    display: block;
    position: relative;
    float: left;
    margin-top: 61px;
    margin-left: 49px;
    width: 219px;
    height: 39px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__enter{
    display: block;
    position: relative;
    float: right;
    margin-right: 77px;
    margin-top: 17px;
    z-index: 1;
    .tel{
      display: block;
      position: relative;
      float: left;
      margin-right: 52px;
      color: #ffffff;
      font-family: $HelveticaNeueCondensedBold;
      font-size: 33px;
    }
    .enter{
      display: block;
      position: relative;
      float: left;
      margin-top: 9px;
      &__icon{
        display: block;
        position: absolute;
        top: 0;
        left: -8px;
        width: 19px;
        height: 19px;
        background-image: url("../img/icons/enter-icon.svg");
        background-repeat: no-repeat;
      }
      &__text{
        display: block;
        position: relative;
        float: left;
        padding-left: 19px;
        @include border-box;
        color: #ffffff;
        font-family: $HelveticaNeueCondensedBold;
        font-size: 18px;
      }
    }
  }
  &__title{
    display: block;
    position: relative;
    float: left;
    width: 100%;
    margin-top: 153px;
    margin-bottom: 155px;
    padding: 0 20px;
    @include border-box;
    text-align: center;
    color: #ffffff;
    font-family: $HelveticaNeueCondensedBold;
    font-size: 64px;
    font-weight: 700;
    letter-spacing: 2px;
    line-height: 74px;
  }
  &__form{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    position: relative;
    float: left;
    width: 100%;
    /*margin-left: calc(50% - (570px/2));*/
  }
  &__name{
    display: block;
    position: relative;
    float: left;
    width: 280px;
    margin-right: 5px;
    border: none;
    border-radius: 2px;
    background-color: rgba(253, 253, 253, 0.86);
    padding: 19px 25px;
    @include border-box;
    color: #605f5f;
    font-family: $HelveticaNeueCyrLight;
    font-size: 18px;
  }
  &__phonenumber{
    display: block;
    position: relative;
    float: left;
    width: 280px;
    margin-right: 5px;
    border: none;
    border-radius: 2px;
    background-color: rgba(253, 253, 253, 0.86);
    padding: 19px 25px;
    @include border-box;
    color: #605f5f;
    font-family: $HelveticaNeueCyrLight;
    font-size: 18px;
  }
  &__submit{
    display: block;
    position: relative;
    float: left;
    width: 178px;
   /* margin-top: 5px;
    padding: 19px 0;*/
    @include border-box;
    border: none;
    border-radius: 2px;
    background-image: linear-gradient(180deg, #01c462 0%, #1fc491 100%);
    color: #ffffff;
    font-family: $HelveticaNeueCyrMedium;
    font-size: 20px;
  }
  &__partners{
    display: block;
    position: relative;
    float: left;
    margin-top: 71px;
    margin-bottom: 23px;
    width: 618px;
    margin-left: calc(50% - (618px / 2));
    text-align: center;
    color: #ffffff;
    font-family: $HelveticaNeueCyrMedium;
    font-size: 14px;
    &:before{
      display: block;
      position: absolute;
      content: "";
      top: 6px;
      left: 6px;
      width: 153px;
      height: 7px;
      background-image: url("../img/icons/separator-head-icon.svg");
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;
    }
    &:after{
      display: block;
      position: absolute;
      content: "";
      top: 5px;
      right: 6px;
      width: 153px;
      height: 7px;
      background-image: url("../img/icons/separator-head-icon.svg");
      background-repeat: no-repeat;
      transform: rotate(180deg);
      -webkit-background-size: contain;
      background-size: contain;
    }
    &--items{
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin-bottom: 37px;
      float: left;
      position: relative;
    }
    &--item{
      display: block;
      position: relative;
      float: left;
      margin: 0 25px;
    }
  }
}
