@media only screen and (max-width: 1024px) {
  .header {
    background-image: url("../img/header-table-bg.png");
  }
  .header__logo {
    margin-top: 80px;
    margin-left: 10px;
  }
  .header__enter .enter__text {
    padding-left: 19px;
    font-size: 13px;
  }
  .header__enter {
    margin-right: 13px;
    margin-top: 36px;
    z-index: 1;
  }
  .header__enter .tel {
    font-size: 24px;
  }
  .header__enter .enter {
    margin-top: 8px;
  }
  .header__navigation {
    margin-top: -25px;
  }
  .header__nav .nav-top {
    width: calc(100% - 63px);
    float: right;
    padding: 0;
    margin: 0;
  }
  .header__nav .nav-top li a {
    font-size: 12px;
  }
  .header__title {
    width: 100%;
    margin-top: 136px;
    margin-bottom: 122px;
    padding: 0 20px;
    text-align: center;
    font-size: 48px;
    font-weight: 700;
    letter-spacing: 2px;
    line-height: 58px;
  }
  .header__name, .header__phonenumber {
    width: 184px;
  }
  /*.header__submit {
    margin-top: 0;
  }*/

  .work__tabs--nav .tab {
    width: 33%;
  }
  .work__tabs-content #forWho {
    padding: 0 24px;
    @include border-box;
    background-image: url("../img/work-tab-table-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
  }
  .work__tabs-content #effect {
    padding: 0 24px;
    @include border-box;
    background-image: url("../img/work2-tab-mob-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
  }
  .work__tabs-content #startWork {
    padding: 0 24px;
    @include border-box;
    background-image: url("../img/work-tab-mob-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .work__tabs-content--title {
    width: 370px;
    height: 68px;
    margin-bottom: 24px;
    margin-top: 69px;
    padding-top: 18px;
    @include border-box;
    text-align: center;
    color: #fff;
    font-size: 24px;
  }
  .work__tabs-content--subtitle {
    width: 588px;
    color: #585858;
    font-size: 18px;
    line-height: 31px;
  }
  .work__tabs-content ul {
    display: block;
    position: relative;
    width: 588px;
    padding: 0;
    margin-left: 0;
  }
  .work__tabs-content ul li {
    font-size: 20px;
    line-height: 42px;
  }

  .why__line {
    padding: 0 24px;
    @include border-box;
    width: 100%;
  }
  .why__item {
    padding: 0 18px;
  }

  .panel__map {
    width: 472px;
    height: 296px;
    background-image: url("../img/Russia-map-tab.png");
    background-repeat:no-repeat;
    img{
      display: none;
    }
  }
  .panel__map .city {
    display: none;
  }
  .panel__leftside, .panel__rightside {
    width: calc((100% - 472px)/2);
  }

  .mob-app__right {
    width: 100%;
    float: left;
    margin-top: 15px;
  }

  .sidePanel {
    display: none;
  }


  .navid-bara__wrap {
    width: 74%;
  }

  .start-now form {
    width: 80%;
  }

  .footer__about {
    width: 25%;
  }

  .footer__about {
    p {
      font-size: 9px;
    }
  }

  .footer__soc {
    &_item {
      size: 25px;
    }
  }

  .footer__list {
    width: 75%;
    li {
      a {
        font-size: 16px;
      }
    }
  }

  .footer__list ul li {
    a {
      font-size: 10px;
    }
  }

  .footer__text {
    width: 32%;
    h2 {
      font-size: 16px;
    }
    p {
      font-size: 9px;
    }
  }
  .footer__text form {
    width: 267px;
    bottom: -32px;
    right: 0;
    input {
      width: 147px;
    }
  }
  .web-panel__left {
    width: 100%;
    padding-left: 0;
  }

  .web-panel__right, .mob-app__left {
    width: 100%;
    display: none;
  }

  .web-panel__hover--trigger {
    width: 100%;
    margin-top: 15px;
    padding-left: 20px;

  }

  .mob-app__hover--trigger{
    width: 100%;
    margin-top: 15px;
    padding-left: 20px;

  }
  .web-panel__hover--box{
    width: 100%;
    padding-left: 20px;
  }
  .mob-app__hover--box{
    width: 100%;
    padding-left: 20px;
  }

  .web-panel__hover--box {
    width: 100%;
    padding-left: 20px;
  }
  .web-panel__slider .owl-dots {
    margin-top: 13px;
  }
  .web-panel__tabs-content, .web-panel__tabs, .mob-app__tabs-content, .mob-app__tabs {
    display: none;
  }
  .web-panel__hover {
    display: block;
    width: 100%;
  }

  .web-panel__wrap, .mob-app__wrap {
    width: 50%;
    float: left;
  }
  .mob-app__wrap{
    width: 100%;
  }

  .web-panel__wrap {
    width: 50%;
    float: left;
  }
  .medium-title {

    padding: 0 10px;
    box-sizing: border-box;
  }

  .mob-app__hover {
    display: block;
    width: 100%;
    float: left;
  }
  .mob-app__slider{
    width: 50%;
    float: left;
    margin-top: 15px;
  }
  .mob-app{
    padding-bottom: 25px;
    box-sizing: border-box;
  }
  .panel__map{
    width: 500px;
  }
  .panel__leftside, .panel__rightside{
    width: calc((100% - 500px)/2);
  }

  .panel__counter--item{
    .number{
      font-size: 30px;
    }
    .title{
      font-size: 14px;
    }
  }
}

