    letter-spacing: 2px;
    line-height: 32px;
  }
  .header__partners {
    display: block;
    margin-top: 39px;
    margin-bottom: 18px;
    overflow: hidden;
    width: 100%;
    text-align: center;
    margin-left: 0;
  }
  .header__partners:before {
    top: 6px;
    left: -108px;
  }
  .header__partners:after {
    top: 5px;
    right: -108px;
  }
  .header__form {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
