.panel{
  display: block;
  position: relative;
  width: 100%;
  float: left;
  background-image: linear-gradient(180deg, #17435f 0%, #285c75 100%);
  &__counter{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    position: relative;
    float: left;
    margin-top: 60px;
    padding: 0 10%;
    @include border-box;
    &--item{
      flex: 1 0 0;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      position: relative;
      float: left;
      align-items: center;
      .number{
        display: block;
        position: relative;
        width: 100%;
        text-align: center;
        color: #ffffff;
        font-family: $HelveticaNeueCyrMedium;
        font-size: 48px;
      }
      .title{
        display: block;
        position: relative;
        width: 100%;
        text-align: center;
        color: #ffffff;
        font-family: $HelveticaNeueCyrLight;
        font-size: 20px;
      }
    }
  }
  &__leftside{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 112px;
    @include border-box;
    width: calc((100% - 730px)/2);
    height: 100%;
    float: left;
    span{
      display: block;
      position: relative;
      float: left;
      opacity: 0.7;
      color: #657e90;
      font-family: $HelveticaNeueCondensedBold;
      font-size: 18px;
      line-height: 43px;
      cursor: pointer;
      &:hover{
        opacity: 1;
      }
    }
  }
  &__map{
    display: block;
    position: relative;
    width: 730px;
    height: 456px;
    margin-bottom: 47px;
    float: left;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    .city{
      display: block;
      position: absolute;
      content: "";
      width: 5px;
      height: 5px;
      border-radius: 50%;
      border: 2px solid #5e8ba5;
      background-color: #20f6ff;
      &:hover{
        background-color: red;
      }
    }
    #city1{
      top: 276px;
      left: 76px;
    }
    #city2{
      top: 283px;
      left: 146px;
    }
    #city3{
      top: 296px;
      left: 108px;
    }
    #city4{
      top: 312px;
      left: 95px;
    }
    #city5{
      top: 315px;
      left: 54px;
    }
    #city6{
      top: 327px;
      left: 126px;
    }
    #city7{
      top: 345px;
      left: 217px;
    }
    #city8{
      top: 352px;
      left: 191px;
    }
    #city9{
      top: 356px;
      left: 110px;
    }
    #city10{
      top: 311px;
      left: 616px;
    }
    #city11{
      top: 367px;
      left: 64px;
    }
    #city12{
      top: 374px;
      left: 79px;
    }
  }
  &__rightside{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 112px;
    @include border-box;
    width: calc((100% - 730px)/2);
    height: 100%;
    float: left;
    span{
      display: block;
      position: relative;
      float: left;
      opacity: 0.7;
      color: #657e90;
      font-family: $HelveticaNeueCondensedBold;
      font-size: 18px;
      line-height: 43px;
      cursor: pointer;
      &:hover{
        opacity: 1;
      }
    }
  }
}