.footer{
  width: 100%;
  float: left;
  position: relative;
  background-color: #1b4257;
  padding: 50px 70px;
  box-sizing: border-box;
}

.footer__about{
  width: 20%;
  float: left;
  position: relative;
  p{
    font-size: 12px;
    margin: 0;
    width: 100%;
    font-family: $HelveticaNeueCyrMedium;
    color: #cacaca;
    line-height: 25px;
    display: inline-block;
  }
}

.footer__soc{
  width: 100%;
  float: left;
  position: relative;
  margin-top: 15px;
  display: none;
  &_item{
    size: 32px;
    float: left;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    background-color: #183649;
    margin-right: 10px;
    img{
      size: 100%;
      object-fit: contain;
    }
    &:last-of-type{
      margin-right: 0;
    }
  }
}

.footer__logo{

  width: 100%;
  float: left;
  position: relative;
  text-align: left;
  overflow: hidden;
  img{
    object-fit: contain;
    height: 39px;
    width: 132px;
  }
}
.footer__list{
  width: 57%;
  float: left;
  position: relative;
  padding: 0;
  margin: 0;
  display: block;
  li{
    list-style-type: none;
    width: 25%;
    float: left;
    display: inline-block;
    color: #657e90;
    font-family: $HelveticaNeueCyrBold;
    font-size: 18px;
    text-transform: uppercase;
    a{
      color: #657e90;
      font-family: $HelveticaNeueCyrBold;
      font-size: 18px;
      text-transform: uppercase;
    }
  }
}

.footer__list ul{
  width: 100%;
  float: left;
  display: inline-block;
  padding: 0;
  margin-top: 20px;
}

.footer__list ul li{
  padding: 0;
  color: #cacaca;
  font-family: $HelveticaNeueCyrMedium;
  font-size: 13px;
  width: 100%;
  display: inline-block;
  margin: 10px 0;
  a{
    color: #cacaca;
    font-family: $HelveticaNeueCyrMedium;
    font-size: 13px;
  }
}

.footer__text{
  width: 23%;
  float: right;
  position: relative;
  text-align: right;
  margin-top: -6px;
  display: none;
  h2{
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    font-family: $HelveticaNeueCyrBold;
    margin: 0;
    width: 100%;
    display: inline-block;
  }
  p{
    color: #cacaca;
    font-family: $HelveticaNeueCyrMedium;
    font-size: 12px;
    font-weight: 500;
    line-height: 31px;
    width: 100%;
    text-align: right;
    margin: 20px 0 0;
    float: right;
    display: inline-block;
  }
  form{
    width: 357px;
    right: 0;
    display: inline-block;
    position: absolute;
        bottom: -90px;
    input{
      width: 238px;
      height: 32px;
      border-radius: 2px;
      border: 1px solid #999999;
      background-color: #ffffff;
      padding-left: 15px;
      float: left;
      &:placeholder{
        color: #d2d2d2;
        font-size: 11px;
        font-family: $HelveticaNeueCyrMedium;
      }
    }
    button{
      min-width: 97px;
      padding: 10px 0px;
      margin-left: 5px;
      box-sizing: border-box;
      border-radius: 2px;
      background-image: -webkit-linear-gradient(#01c462 0%, #1fc491 100%);
      background-image: -o-linear-gradient(#01c462 0%, #1fc491 100%);
      background-image: linear-gradient(#01c462 0%, #1fc491 100%);
      font-size: 13px;
      font-family: $HelveticaNeueCyrMedium;
      color: #fff;
      display: inline-block;
      float: left;
      border: none;
      cursor: pointer;
      outline: none;
    }
  }
}


.copiright{
  width: 100%;
  float: left;
  position: relative;
  text-align: left;
  background-color: #183649;
  padding: 25px 70px;
  box-sizing: border-box;
  p{
    margin: 0;
    width: 100%;
    float: left;
    display: inline-block;
    font-size: 13px;
    color: #c9c9c9;
    font-family: $HelveticaNeueCyrMedium;
  }
}
