.sidePanel{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 26px;
  height: 190px;
  top: calc(50% - (190px/2));
  right: 23px;
  z-index: 10;
  a{
    display: block;
    position: relative;
    width: 13px;
    height: 13px;
    background-color: #426980;
    color: transparent;
    border-radius: 50%;
    border: none;
    margin: 3px 0;
    span{
      display: block;
      position: relative;
      width: 100%;
      text-align: center;
      line-height: 25px;
      color: transparent;
      font-family: $HelveticaNeueCyrMedium;
      font-size: 13px;
    }
  }
  a.active{
    width: 26px;
    height: 26px;
    span{
      color: #133e5a;
    }
    &:before{
      display: block;
      position: absolute;
      content: "";
      top: 4px;
      left: 4px;
      width: 18px;
      height: 18px;
      background-color: #fff;
      color: #133e5a;
      font-family: $HelveticaNeueCyrMedium;
      font-size: 13px;
      border-radius: 50%;
    }
  }
}