.navid-bara{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  background-image: url("../img/navid-baraty-fon.png");
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  .work__title{
    color: #fff;
  }

}

.navid-bara__wrap{
  width: 50%;
  float: none;
  margin: 0 auto;
  .navid-bara__wrap_item{
    width: 50%;
    float: left;
    position: relative;
    .thumb{
      width: 50%;
      float: left;
      text-align: center;
    }
    p{
      float: left;
      width: 100%;
      text-align: center;
      color: #fff;
      font-size: 13px;
      font-family: $HelveticaNeueCyrLight;
    }
    .price{
      float:left;
      width: 50%;
      position: relative;
      margin-top: 20px;
      span{
        float: left;
        font-size: 36px;
        color: #fff;
        font-family: $HelveticaNeueCyrLight;
      }
      p{
        float: left;
        width: 25px;
        margin: 0;
        margin-left: 10px;
        margin-top: 4px;
        span{
          color: #fff;
          font-family: $HelveticaNeueCyrLight;
          float: left;
          font-size: 13px;
          width: 100%;
          position: relative;
          &:last-of-type{
            &:before{
              content: "";
              width: 100%;
              height: 1px;
              background-color: #fff;
              display: inline-block;
              position: absolute;
              left: 0;
            }
          }
        }
      }
    }
  }
  .test-plan{
    min-width: 121px;
    padding: 15px 25px;
    box-sizing: border-box;
    border-radius: 2px;
    background-image: -webkit-linear-gradient(#01c462 0%, #1fc491 100%);
    background-image: -o-linear-gradient(#01c462 0%, #1fc491 100%);
    background-image: linear-gradient(#01c462 0%, #1fc491 100%);
    font-size: 20px;
    font-family: $HelveticaNeueCyrLight;
    color: #fff;
    display: inline-block;
    margin: 50px auto 0;
    float: none;
    }
}

.small-title{
  color: #fff;
  font-size: 20px;
  font-family: $HelveticaNeueCyrLight;
  margin-bottom: 40px;
  display: inline-block;
  &:before{
    content: "";
    background-image: url("../img/left-line.png");
    width: 152px;
    height: 6px;
    display: inline-block;
    position: relative;
    top: -3px;
    margin-right: 40px;
  }
  &:after{
    content: "";
    background-image: url("../img/right-line.png");
    width: 152px;
    height: 6px;
    display: inline-block;
    position: relative;
    top: -3px;
    margin-left: 40px;
  }
}
